:root {
  --theme1: rgb(33, 33, 33);
  --theme2: white;
  --theme3: rgb(129, 152, 163);
}

.maindivv {
  background-color: var(--theme1);
}

/* main content */
h1 {
  font-family: "Ubuntu Condensed", sans-serif;
  font-size: 300%;
  color: var(--theme2);
}
br {
  visibility: hidden;
}
h1::selection {
  background-color: var(--theme2);
  color: var(--theme1);
}
.coll1 {
  padding: 10% 0% 14% 14%;
}
.coll2 {
  padding: 10% 10% 10% 15%;
}
